import style from '../styles/index.scss';
import QrCode from "qrcode-reader";

window.addEventListener('DOMContentLoaded', () => {
    function debug(str){
        document.getElementById('debug').textContent = str.toString();
    }
    function showErrorScreen(error) {
        const errorScreen = document.createElement('div');
        errorScreen.classList.add(style.errorScreen);
        errorScreen.innerHTML = error;
        document.body.append(errorScreen);
    };

    if (!('mediaDevices' in navigator &&
        'getUserMedia' in navigator.mediaDevices)) {
        showErrorScreen(`К сожалению ваш браузер или устройство не поддерживает работу с камерой`);
        return;
    }

    const snapshotCanvas = document.getElementById('snapshot');
    const snapshotContext = snapshotCanvas.getContext('2d');
    const video = document.getElementById('camera');
    const flipCameraButton = document.getElementById("flipCamera");
    const overlay = document.getElementById('snapshotLimitOverlay');

    let snapshotSquare;
    function calculateSquare() {
        const snapshotSize = overlay.offsetWidth;
        snapshotSquare = {
            x: +((video.videoWidth - snapshotSize)/2),
            y: +((video.videoHeight - snapshotSize)/2),
            size: +(snapshotSize)
        };

        snapshotCanvas.width = snapshotSquare.size;
        snapshotCanvas.height = snapshotSquare.size;
    }

    function scanCode(long){
        setTimeout(function(){
            snapshotContext.drawImage(
                video,
                snapshotSquare.x,
                snapshotSquare.y,
                snapshotSquare.size,
                snapshotSquare.size,
                0,
                0,
                snapshotSquare.size,
                snapshotSquare.size
            );
            const imageData = snapshotContext.getImageData(0, 0, snapshotSquare.size, snapshotSquare.size);
            const qr = new QrCode();
            qr.callback = (err, value) => {
                if(err){
                    console.warn(err);
                    debug(err, false);
                    scanCode();
                    return;
                }
                if(value.result){
                    debug(value.result, true);
                    scanCode(true);
                } else {
                    scanCode();
                }

            };
            qr.decode({width: snapshotSquare.size, height: snapshotSquare.size}, imageData);



            },long?2000:200);
    }

    let currentDeviceId;
    function initVideoStream () {
        let config = {
            audio: false,
            video: {}
        };
        config.video = currentDeviceId ? {deviceId: currentDeviceId} : {facingMode: "environment"};
        stopStream();

        navigator.mediaDevices.getUserMedia(config).then(function (stream) {
            video.srcObject = stream;
            video.oncanplay = function(){
                calculateSquare();
                scanCode();
            };
        }).catch(function (error) {
            alert(error.name + ": " + error.message);
        });
    }

    function stopStream() {
        if (video.srcObject) {
            video.srcObject.getTracks()[0].stop();
        }
    }
    initVideoStream();

    navigator.mediaDevices.enumerateDevices()
        .then(function(devices) {
            devices = devices.filter(function (device) {
                return device.kind === 'videoinput';
            });
            if (devices.length > 1) {
                // add a flip camera button
                flipCameraButton.style.display = "block";
                currentDeviceId = devices[0].deviceId;

                flipCameraButton.addEventListener('click', function() {
                    let targetDevice;
                    for (let i = 0; i < devices.length; i++) {
                        if (devices[i].deviceId === currentDeviceId) {
                            targetDevice = (i + 1 < devices.length) ? devices[i+1] : devices[0];
                            break;
                        }
                    }
                    currentDeviceId = targetDevice.deviceId;
                    initVideoStream();
                });
            }
        });

    document.addEventListener("visibilitychange", function() {
        if (document.hidden) {
            stopStream();
        } else {
            initVideoStream();
        }
    });

});
